.navbar{
    max-width: 100%;
    background-color: var(--bgColorOne);
    display: flex;
    justify-content: space-around;
    align-items: center;
}

.navbar span{
    color: white;
}

.navbarTitle{
    letter-spacing: .2rem;
}

.navbarTitle > h1 {
    font-family:'Lucida Sans', 'Lucida Sans Regular', 'Lucida Grande', 'Lucida Sans Unicode', Geneva, Verdana, sans-serif;
    font-size: 1.8rem;
}

.addButton > button{
    background-color: var(--bgColorThree);
    cursor: pointer;
    padding: .4rem 1rem;
    font-size: .9rem !important;  
    border: solid 3px var(--bgColorTwo);
    border-radius: 5px;
}

.menuButton,
.addButton > button{
    cursor: pointer;
    transition: transform .2s ease;
}

.menuButton:hover,
.addButton > button:hover{
    transform: scale(1.05);
}

.menuButton:active,
.addButton > button:active{
    transform: scale(1.1);
}

.githubLogo > a > img {
    width: 30px;
    transition: .2s width ease;
}

a > img {
    width: 100%;
}

.githubLogo > a > img:hover{
    width: 35px;
    opacity: .8;
}

.addButton > button:hover{
    background-color: var(--bgColorTwo);
}`, "",{"version":3,"sources":["webpack://./src/css/navbar.css"],"names":[],"mappings":";AACA;IACI,eAAe;IACf,mCAAmC;IACnC,aAAa;IACb,6BAA6B;IAC7B,mBAAmB;AACvB;;AAEA;IACI,YAAY;AAChB;;AAEA;IACI,qBAAqB;AACzB;;AAEA;IACI,qHAAqH;IACrH,iBAAiB;AACrB;;AAEA;IACI,qCAAqC;IACrC,eAAe;IACf,mBAAmB;IACnB,2BAA2B;IAC3B,mCAAmC;IACnC,kBAAkB;AACtB;;AAEA;;IAEI,eAAe;IACf,8BAA8B;AAClC;;AAEA;;IAEI,sBAAsB;AAC1B;;AAEA;;IAEI,qBAAqB;AACzB;;AAEA;IACI,WAAW;IACX,0BAA0B;AAC9B;;AAEA;IACI,WAAW;AACf;;AAEA;IACI,WAAW;IACX,WAAW;AACf;;AAEA;IACI,mCAAmC;AACvC","sourcesContent":["\n.navbar{\n    max-width: 100%;\n    background-color: var(--bgColorOne);\n    display: flex;\n    justify-content: space-around;\n    align-items: center;\n}\n\n.navbar span{\n    color: white;\n}\n\n.navbarTitle{\n    letter-spacing: .2rem;\n}\n\n.navbarTitle > h1 {\n    font-family:'Lucida Sans', 'Lucida Sans Regular', 'Lucida Grande', 'Lucida Sans Unicode', Geneva, Verdana, sans-serif;\n    font-size: 1.8rem;\n}\n\n.addButton > button{\n    background-color: var(--bgColorThree);\n    cursor: pointer;\n    padding: .4rem 1rem;\n    font-size: .9rem !important;  \n    border: solid 3px var(--bgColorTwo);\n    border-radius: 5px;\n}\n\n.menuButton,\n.addButton > button{\n    cursor: pointer;\n    transition: transform .2s ease;\n}\n\n.menuButton:hover,\n.addButton > button:hover{\n    transform: scale(1.05);\n}\n\n.menuButton:active,\n.addButton > button:active{\n    transform: scale(1.1);\n}\n\n.githubLogo > a > img {\n    width: 30px;\n    transition: .2s width ease;\n}\n\na > img {\n    width: 100%;\n}\n\n.githubLogo > a > img:hover{\n    width: 35px;\n    opacity: .8;\n}\n\n.addButton > button:hover{\n    background-color: var(--bgColorTwo);\n}"],"sourceRoot":""}]);
