___CSS_LOADER_EXPORT___.push([module.id, `body{
    box-sizing: border-box;
    margin: 0;
    padding: 0;
    height: 100vh;
    display: grid;
    grid-template-rows:  60px 1fr;
    grid-template-columns: 250px 1fr;
    background-color: var(--bgColorOne);
    --textColor: #FCFFF7;
    --bgColorOne: #243141;
    --bgColorTwo: #162338;
    --bgColorThree: #1E2A38;
    --colorRed: rgb(195, 0, 0);
    transition: grid-template-columns 0.5s ease-out;
} 

body > div.sideBar {
    transition: width 0.5s ease-out;
}

body.close{
    grid-template-columns: 100px 1fr;
}

/* narBar */
body > div:first-child{
    grid-column: 2/-1;
}

body > div.sideBar{
    grid-row: 1/-1;
}

h1,
h2,
h3,
p,
button,
label{
    color: var(--textColor);
    font-family: Arial, Helvetica, sans-serif;
    border: none;
    margin: 0;
}

.content{
}

.blurBackground{
    border: solid 1px red;
    filter: blur(5px);
}

.overlay{
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background-color: rgba(0, 0, 0, 0.5); 
    backdrop-filter: blur(5px);        
    z-index: 10;
}

.invalid{
    border-color: var(--colorRed) !important;
    transition: border-color .2s ease;
}`, "",{"version":3,"sources":["webpack://./src/css/general.css"],"names":[],"mappings":"AAAA;IACI,sBAAsB;IACtB,SAAS;IACT,UAAU;IACV,aAAa;IACb,aAAa;IACb,6BAA6B;IAC7B,gCAAgC;IAChC,mCAAmC;IACnC,oBAAoB;IACpB,qBAAqB;IACrB,qBAAqB;IACrB,uBAAuB;IACvB,0BAA0B;IAC1B,+CAA+C;AACnD;;AAEA;IACI,+BAA+B;AACnC;;AAEA;IACI,gCAAgC;AACpC;;AAEA,WAAW;AACX;IACI,iBAAiB;AACrB;;AAEA;IACI,cAAc;AAClB;;AAEA;;;;;;IAMI,uBAAuB;IACvB,yCAAyC;IACzC,YAAY;IACZ,SAAS;AACb;;AAEA;AACA;;AAEA;IACI,qBAAqB;IACrB,iBAAiB;AACrB;;AAEA;IACI,eAAe;IACf,MAAM;IACN,OAAO;IACP,WAAW;IACX,YAAY;IACZ,oCAAoC;IACpC,0BAA0B;IAC1B,WAAW;AACf;;AAEA;IACI,wCAAwC;IACxC,iCAAiC;AACrC","sourcesContent":["body{\n    box-sizing: border-box;\n    margin: 0;\n    padding: 0;\n    height: 100vh;\n    display: grid;\n    grid-template-rows:  60px 1fr;\n    grid-template-columns: 250px 1fr;\n    background-color: var(--bgColorOne);\n    --textColor: #FCFFF7;\n    --bgColorOne: #243141;\n    --bgColorTwo: #162338;\n    --bgColorThree: #1E2A38;\n    --colorRed: rgb(195, 0, 0);\n    transition: grid-template-columns 0.5s ease-out;\n} \n\nbody > div.sideBar {\n    transition: width 0.5s ease-out;\n}\n\nbody.close{\n    grid-template-columns: 100px 1fr;\n}\n\n/* narBar */\nbody > div:first-child{\n    grid-column: 2/-1;\n}\n\nbody > div.sideBar{\n    grid-row: 1/-1;\n}\n\nh1,\nh2,\nh3,\np,\nbutton,\nlabel{\n    color: var(--textColor);\n    font-family: Arial, Helvetica, sans-serif;\n    border: none;\n    margin: 0;\n}\n\n.content{\n}\n\n.blurBackground{\n    border: solid 1px red;\n    filter: blur(5px);\n}\n\n.overlay{\n    position: fixed;\n    top: 0;\n    left: 0;\n    width: 100%;\n    height: 100%;\n    background-color: rgba(0, 0, 0, 0.5); \n    backdrop-filter: blur(5px);        \n    z-index: 10;\n}\n\n.invalid{\n    border-color: var(--colorRed) !important;\n    transition: border-color .2s ease;\n}"],"sourceRoot":""}]);
